const barcodeDetector =
  window.BarcodeDetector !== undefined
    ? new window.BarcodeDetector()
    : undefined;

export const NFC = () => {
  alert("현재 NFC 기능은 지원되지 않습니다.");
};

export const Bluetooth = () => {
  const serviceUUID = "6a48d8f0-0b6f-6b30-5941-f597c5ed012d";
  const characteristicUUID = "b75b1617-6a75-361c-ed56-50bbb74e8a58";

  navigator.bluetooth
    .requestDevice({
      filters: [
        {
          namePrefix: "ScanBuy"
        }
      ],
      optionalServices: [serviceUUID]
    })
    .then(device => {
      return device.gatt.connect();
    })
    .then(server => {
      return server.getPrimaryService(serviceUUID);
    })
    .then(service => {
      return service.getCharacteristic(characteristicUUID);
    })
    .then(characteristic => {
      return characteristic.readValue();
    })
    .then(value => {
      const productID = value.getUint8(0);
      console.log(`Product's ID is ${productID}`);
      // TODO get Product Info and show popup
    })
    .catch(error => {
      console.log(error);
    });
};

export const CameraOn = () => {};

export const Barcode = image => {
  barcodeDetector.detect(image).then(barcodes => {
    if (barcodes.length !== 1) return;

    const barcode = barcodes[0];
    console.log(barcode.rawValue);
    // TODO Pause camera
    // show popup
  });
};
