import React from "react";
import "./Item.css";

const Item = ({ item }) => {
  const { id, name, price, amount } = item;
  return (
    <div className="Item" item-id={id}>
      <img className="Item-img" alt={name} />
      <div className="Item-text-wrap">
        <div className="Item-name">{name}</div>
        <div className="Item-price">{price}</div>
      </div>
      <div className="Item-etc">
        <div className="Item-up-btn Item-btn">^</div>
        <div className="Item-amount">{amount}</div>
        <div className="Item-down-btn Item-btn">V</div>
        <div className="Item-delete-btn Item-btn">X</div>
      </div>
    </div>
  );
};

export default Item;
