import React from "react";
import Item from "./Item";
import Pay from "./Pay";
import "./Cart.css";

const Cart = () => {
  const items = [
    {
      id: 1,
      name: "Item1",
      price: 5000,
      amount: 1
    },
    {
      id: 2,
      name: "Item2",
      price: 10000,
      amount: 1
    }
  ].map(item => <Item item={item} key={item.id} />);

  return (
    <div className="Cart">
      <div className="Cart-items">{items}</div>
      <div className="Cart-pay">
        <Pay price="10000" />
      </div>
    </div>
  );
};

export default Cart;
