import React, { useEffect, useRef } from "react";
import "./Camera.css";

const Camera = () => {
  const loadImg = useRef(null);
  const camVideo = useRef(null);
  const camCanvas = useRef(null);

  useEffect(() => {
    console.log(1);
    navigator.mediaDevices
      .getUserMedia({ video: true })
      .then(stream => {
        camVideo.current.srcObject = stream;
      })
      .catch(error => {
        console.log(error);
      });
  });
  return (
    <div className="Camera">
      <img className="Camera-load" alt="Camera Loading" ref={loadImg} />
      <div>
        <video className="Camera-video Camera-screen" autoPlay ref={camVideo} />
        <canvas className="Camera-canvas Camera-screen" ref={camCanvas} />
      </div>
    </div>
  );
};

export default Camera;
